.season-display {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .icon-left {
    position: absolute;
    top: 10px;
    left: 10px;
  }
  
  .icon-right {
    position: absolute;
    bottom: 10px;
    right: 10px;
  }

 
  .season-display.spring i {
    color: yellow
}

.season-display.summer i {
    color: red;
}

.season-display.autumn i {
    color: orangered;
}

.season-display.winter i {
    color: blue;
}


.spring {
    background-color: rgb(110, 223, 110);
}


.summer {
    background-color: rgb(245, 161, 6);
}

.autumn {
    background-color: rgb(194, 79, 95);
}

.winter {
    background-color: rgb(108, 126, 141);
}
